.button {
    position: relative;
    display: inline-block;
    box-sizing: border-box;
    border: none;
    border-radius: 4px;
    padding: 0 16px;
    min-width: 64px;
    height: 36px;
    vertical-align: middle;
    text-align: center;
    text-overflow: ellipsis;
    text-transform: uppercase;
    color: rgb(255, 255, 255);
    background-color: rgb(33, 150, 243);
    box-shadow: 0 3px 1px -2px rgba(0, 0, 0, 0.2), 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12);
    font-size: 14px;
    font-weight: 500;
    line-height: 36px;
    overflow: hidden;
    outline: none;
    cursor: pointer;
    transition: box-shadow 0.2s;
}

.button::-moz-focus-inner {
    border: none;
}

/* Overlay */
.button::before {
    content: "";
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: rgb(255, 255, 255);
    opacity: 0;
    transition: opacity 0.2s;
}

/* Ripple */
.button::after {
    content: "";
    position: absolute;
    left: 50%;
    top: 50%;
    border-radius: 50%;
    padding: 50%;
    width: 32px; /* Safari */
    height: 32px; /* Safari */
    background-color: rgb(255, 255, 255);
    opacity: 0;
    transform: translate(-50%, -50%) scale(1);
    transition: opacity 1s, transform 0.5s;
}

/* Hover, Focus */
.button:hover,
.button:focus {
    box-shadow: 0 2px 4px -1px rgba(0, 0, 0, 0.2), 0 4px 5px 0 rgba(0, 0, 0, 0.14), 0 1px 10px 0 rgba(0, 0, 0, 0.12);
}

.button:hover::before {
    opacity: 0.08;
}

.button:focus::before {
    opacity: 0.24;
}

.button:hover:focus::before {
    opacity: 0.3;
}

/* Active */
.button:active {
    box-shadow: 0 5px 5px -3px rgba(0, 0, 0, 0.2), 0 8px 10px 1px rgba(0, 0, 0, 0.14), 0 3px 14px 2px rgba(0, 0, 0, 0.12);
}

.button:active::after {
    opacity: 0.32;
    transform: translate(-50%, -50%) scale(0);
    transition: transform 0s;
}

/* Disabled */
.button:disabled {
    color: rgba(0, 0, 0, 0.38);
    background-color: rgba( 0, 0, 0, 0.12);
    box-shadow: none;
    cursor: initial;
}

.button:disabled::before {
    opacity: 0;
}

.button:disabled::after {
    opacity: 0;
}

.actionButton {
    position: absolute;
    bottom: 1rem;
    right: 1rem;
}

.field {
  margin: .5rem 0;
}

.field label {
  display: block;
}

.tabActions {
  display: flex;
  justify-content: space-around;
}

.buttonRow {
  padding-left: 1rem;
  padding-bottom: 1rem;
  display: flex;
  align-items: center;
  justify-content: flex-start;
}

/* Modal */
.listedContent {
  display: flex;
  flex-direction: column;
  height: 100%
}

.modalList {
  margin-top: 1rem;
  flex: 1;
  overflow: overlay;
}

.modalItem {
  display: block;
  padding: .25rem 0
}

/* Lists */

.list {
  height: 200px;
  overflow: auto;
  padding: 0;
  margin: 0;
  margin-top: .75rem;
  border: 1px solid rgba(0,0,0,.25)
}

.list li {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding: .5rem 0;
}

.list li span {
  text-align: left;
  padding-left: .5rem;
  display: block;
  flex: 1;
}

.list li input,
.list li select {
  width: auto !important;
}
